import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment as env } from 'src/environments/environment';
import { ApiResponse } from 'src/app/models/api-response.model';

@Injectable({
  providedIn: 'root'
})
export class FixRequestService {

  constructor(private http: HttpClient) { }

  getAllBrands() {
    return this.http.get<ApiResponse>(`${env.baseUrl}DeviceBrand`);
  }
  getAllModels() {
    return this.http.get<ApiResponse>(`${env.baseUrl}DeviceModel`);
  }
  getAllModelsById(id) {
    // DeviceModel?Filtervalue[DevicebrandId]=3
    return this.http.get<ApiResponse>(`${env.baseUrl}DeviceModel?Filtervalue[DevicebrandId]=${id}`);
  }
  // getAllModelsById(id) {
  //   return this.http.get<ApiResponse>(`${env.baseUrl}DeviceModel/${id}` );
  // }
  getAllProblems(id) {
    return this.http.get<ApiResponse>(`${env.baseUrl}DeviceProblemClassification?FilterValue[DeviceModelId]=${id}`);
  }
  getAllBranshes() {
    return this.http.get<ApiResponse>(`${env.baseUrl}Branch`);
  }
  checkAppointment(appointmentData) {
    return this.http.post<ApiResponse>(`${env.baseUrl}RepairRequest`, appointmentData)
  }


  getAllTrackRequest(id) {
    return this.http.get<ApiResponse>(`${env.baseUrl}RepairRequestInfo?FilterValue[repairRequestNumber]=${id}`);
  }

  getAllUserRequests(userId) {
    return this.http.get<ApiResponse>(`${env.baseUrl}RepairRequest?FilterValue[userId]=${userId}&PageSize=100`);
  }
}
